<template>
  <div class>
    <div class="layout">
      <div class="loginmain">
        <div class="loginbox">
          <div class="logininner">
            <div class="logonj"></div>
            <div class="loginform">
              <div class="mtb02">
                <h1>社員コード</h1>
                <Input prefix="ios-contact" placeholder="Enter name" style="width: auto"/>
              </div>
        
              <div class="mtb20">
                <h1>パスワード</h1>
                <Input prefix="ios-key" placeholder="Password" type="password" style="width: auto"/>
              </div>
              <div class="notice">
                <Row :gutter="16">
                    <Col span="12" offset="12">
                        <div class="text-right"><a href="">パスワードを忘れる？</a></div>
                    </Col>
                </Row>
                  
              </div>
              <div class="loginbtn">
                  <Button shape="circle" type="primary">ログイン</Button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "login",
  components: {}
};
</script>
<style scoped>

/*login*/
.loginmain{
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    /* background-image: url(../images/login.jpg);
    background-size: contain;
    background-repeat: no-repeat;
background-attachment:fixed;
background-position: -300px 0;  */
background-image: linear-gradient(120deg, #a1c4fd 0%, #c2e9fb 100%);
}
.loginbox{
    position: absolute;
    width: 40%;
    top: 0;
    bottom: 0;
    right: 0;
    background-color: #fff;
}
.logininner{
    margin: 150px 100px 0 100px;
}
.loginbox .logonj{
    /* background: url(../images/logo.png) no-repeat left; */
    background-size: contain;
    width: auto;
    height: 42px;
    margin-bottom: 50px;
}
.loginform{
    text-align: left;
}
.loginform h1{
    font-size: 16px;
    color: #4e9ff8;
    margin-bottom: 10px;
}
.loginform .ivu-input-wrapper {
    display:block;
    /* width: 100%; */
    position: relative;
    vertical-align: middle;
    line-height: normal;
}
.loginform input{
    width: 100%;
}
.loginform .ivu-input {
    height: 50px;
    line-height: 1.5;
    font-size: 15px;
    font-weight: 600;
    color: #374c91;
    border: none;
    border-radius:0;
    border-bottom: solid 2px #32488e;
    outline: none;
}
.loginform .ivu-input:focus{
    outline: none;
    box-shadow: none;
}
.loginform .ivu-input-prefix i, .ivu-input-suffix i {
    font-size: 30px;
    line-height: 50px;
    color: #d1d9ec;
}
.loginform .ivu-input-with-prefix {
    padding-left: 50px;
}
.loginform .ivu-btn {
    line-height: 2.5em;
    padding: 5px 50px;
    font-size: 16px;
}
</style>




