        height: 30px;
        background: url(images/logo.png) no-repeat center;
        background-size: contain;
        border-radius: 3px;
        float: left;
        position: relative;
        top: 10px;
        left: 20px;
    }
    .layout-nav{
        width: 600px;
        margin: 0 auto;
        margin-right: 20px;
    }

    .layout-Dropdown{
        position: absolute;
        right: 0;
        top:0;
        padding-right: 20px;
        line-height: 50px;
        
    }
    .layout-Dropdown .ivu-dropdown a{
        color: #515a6e;
    }
    .layout-Dropdown .ivu-dropdown i.ivu-icon-md-contact{
        color: #ffcc6a;
        font-size: 2.5em;
        
    }
    .layout-Dropdown .ivu-dropdown-rel{
        height: 45px;
    }

    /**  nav  **/
    .light_nav_customer.ivu-menu-horizontal {
        height: 50px;
        line-height: 50px;
    }
    .light_nav_customer.ivu-menu-light {
        background:transparent;
        text-align: center;
    }
    .light_nav_customer .layout-nav {
        display: inline-block;
        margin: 0 auto;
    }
    .light_nav_customer .ivu-menu-item{
        font-size: 1em;
        font-weight: 600;
    }
    .light_nav_customer.ivu-menu-horizontal.ivu-menu-light:after {
        content: '';
        display: block;
        width: 100%;
        height: 0px;
        background: #dcdee2;
        position: absolute;
        bottom: 0;
        left: 0;
    }

/* footer */ 
.myapp.ivu-layout-footer {
    background: #fff;
    text-align: center;
    padding: 15px 50px;
    border-top: solid 1px #eee;
} 
    
/*menu*/
.sidericon{
  background-color:#f5f7f9;
  height: 40px;
  line-height:40px;
  margin-right:-1px;
}
.ivu-layout-sider-collapsed .sidericon {  
    width: 44px;
    background: #175edc;
}
.ivu-layout-sider-collapsed .menu-icon {  
    width: 44px;
}

.menu-icon {
    transition: all 0.3s;
    width: 40px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    background: #175edc;
    color: #fff; 
}
.rotate-icon {
transform: rotate(-90deg);
}
.ivu-menu span.sidericon{
    position: absolute;
    left: 0;
    display: inline-block; 
    width: 50px;
    text-align: center;
    cursor: pointer;
}
.ivu-layout .ivu-layout-has-sider {
    padding-top: 52px;
    

}
#app-customer .ivu-layout-sider{
    background: url(images/bgmenu.png) repeat-y left #fff;
    border-right: solid 1px #ddd;
}
#app-customer .ivu-layout-sider-collapsed{
    background: #3076f2;
}
    /*// li //*/
.light_menu_customer.ivu-menu-vertical .ivu-menu-item{
    padding: 0;
    position: relative;
    cursor: pointer;
    z-index: 1;
    transition: all .2s ease-in-out;
}
.light_menu_customer.menu-item span{
    display: inline-block;
    overflow: hidden;
    width: 130px;
    text-overflow: ellipsis;
    white-space: nowrap;
    line-height: 1.22em;
    vertical-align: middle;
    transition: width .2s ease .2s;
}
.light_menu_customer.menu-item i{
    width: 40px;
    height: 40px;
    padding: 10px;
    background: #3076f2;
    border-bottom: solid 1px #2260ce;
    border-top: solid 1px #4f8fff;
    color: #fff;
    transform: translateX(0px);
    transition: font-size .2s ease, transform .2s ease;
    vertical-align: middle;
    font-size: 16px;
    text-align: center;
}
    /*// li右边线 //*/
.light_menu_customer.ivu-menu-vertical.ivu-menu-light:after {
    width: 0px;
    background: #fff;
}
    /*// 选中后ICon //*/
.light_menu_customer.ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu) i{
    color: #2d8cf0;
    background: #f0faff;
    z-index: 2;
}
    /*// 选中后li右边线 //*/
.light_menu_customer.ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu):after{
    width: 0px;
    background: #fff;
}

    /*// 折叠后li //*/
.light_menu_customer.collapsed-menu .ivu-menu-item{
    padding: 0;
    background: #3076f2;
    border-bottom: solid 1px #2260ce;
    border-top: solid 1px #4f8fff;
    height: 45px;
    text-align: center;
    line-height: 30px;
}
    /*// 折叠后icon //*/
.light_menu_customer.collapsed-menu i{
    /* transform: translateX(0px); */
    width: 44px;
    height: 45px;
    transition: font-size .2s ease .2s, transform .2s ease .2s;
    vertical-align: middle;
    font-size: 22px;
    color: #fff;
    border: none;
    margin-right: 0;
}
    /*// 折叠后menu文字 //*/
.light_menu_customer.collapsed-menu span{
    width: 0px;
    height: 0px;
    padding: 0;
    transition: width .2s ease;
}
/**/
.ivu-breadcrumb {
    color: #999;
    font-size: 14px;
    padding:0 10px;
    margin-left:-1px;
    height: 40px;
    line-height: 40px;
}

</style>



