<script>
export default {
  name: "Nav",
  components: {},
  data() {
    return {
      formValidate: {
          name: '',
          mail: '',
          city: '',
          gender: '',
          interest: [],
          date: '',
          time: '',
          desc: ''
      },
      ruleValidate: {
          name: [
              { required: true, message: '必填項不能為空', trigger: 'blur' }
          ],
          mail: [
              { required: true, message: '必填項不能為空', trigger: 'blur' },
              { type: 'email', message: 'Incorrect email format', trigger: 'blur' }
          ]
      },
      file: null,
      loadingStatus: false,
      ok: "更 新",
      cancel: "キャンセル",
      modal11: false,
      value1: false,
      formTop: {
        input1: ""
      },
      styles: {
        // height: 'calc(100% - 55px)',
        overflow: "auto",
        paddingtop: "53px",
        // paddingBottom: '53px',
        position: "static"
      },
      formData: {
        name: "",
        url: "",
        owner: "",
        type: "",
        approver: "",
        date: "",
        desc: ""
      },
      formItem: {
        input: "",
        select: "",
        radio: "male",
        checkbox: [],
        switch: true,
        date: "",
        time: "",
        slider: [20, 50],
        textarea: ""
      },
      isCollapsed: false,
      data6: [
        {
          Num: "0101",
          Name: "朝8時固定",
          Form: "固定勤",
          time1: "8:00～17:00",
          time2: "8:00",
          relax1: "7:00～ 8:00",
          relax2: "12:00～13:00",
          relax3: ""
        }
      ],
      editIndex: -1,
      num: ""
    };
  },
  computed: {
    rotateIcon() {
      return ["menu-icon", this.isCollapsed ? "rotate-icon" : ""];
    },
    menuitemClasses() {
      return ["menu-item", this.isCollapsed ? "collapsed-menu" : ""];
    }
  },
  methods: {
    handleSubmit (name) {
            this.$refs[name].validate((valid) => {
                if (valid) {
                    this.$Message.success('Success!');
                } else {
                    this.$Message.error('Fail!');
                }
            })
        },
        handleReset (name) {
            this.$refs[name].resetFields();
        },
    collapsedSider() {
      this.$refs.side1.toggleCollapse();
    },
    show(index) {
      this.$Modal.info({
        title: "下記の勤務体系を廃止してもよらしいですか？",
        content: `勤務体系コー：${this.data6[index].Num}
        <br>
        勤務体系名：${this.data6[index].Name}
        <br>
        勤務形態：${this.data6[index].Form}`
      });
    },
    remove(index) {
      this.data6.splice(index, 1);
    },
    edit(index) {
      this.editIndex = index;
    },
    handleUpload (file) {
        this.file = file;
        return false;
    },
    upload () {
        this.loadingStatus = true;
        setTimeout(() => {
            this.file = null;
            this.loadingStatus = false;
            this.$Message.success('Success')
        }, 1500);
    }
  }
}
</script>

