}
</style>

<script>
export default {
  name: 'StableAdd',
  components: {},
  data: function() {
    return {
      modal: false,
      modal1: false,
      currentStep: 0,
      footerHide: true
    }
  },
  computed: {
    functionC() {
      return ''
    }
  },
  methods: {
    next() {
      if (this.currentStep === 3) {
        return
      } else {
        this.currentStep += 1
      }
    },

    back() {
      if (this.currentStep === 0) {
        return
      } else {
        return (this.currentStep -= 1)
      }
    }
  }
}
</script>



